import React, {Component} from 'react';
import Spinner from 'react-bootstrap/Spinner'
class App extends Component {
  constructor() {
    super();
    this.state =  {buttonState: "Find Video", buttonDisbale: false, urlState: "", imgURL: "", title: "",
                  progressState: "None"}
    this.youtubeURL = "https://noembed.com/embed?url="
    this.apiURL = "http://3.138.182.226:8080/convert/"
  }

  //function for finding and converting video
  convert = () => {
    //condition for when finding the video thumbnail and title
    if (this.state.buttonState === "Find Video") {
      fetch(this.youtubeURL+this.state.urlState)
        .then((response) => response.json())
        .then((json) => {
          //checks if property is there, meaning the youtube video was found
          if ("title" in json) {
            console.log(json)
            this.setState({title: json.title, buttonState: "Download", imgURL: json.thumbnail_url})
            console.log(this.state)
          }
        })
        .catch((error) => {
          console.error(error);
        });
    }
    //condition for when downloading the video
    else {
      this.setState({buttonDisbale: true, progressState: "Block"})
      let youtubeID = this.getParamName("v", this.state.urlState)
      fetch(this.apiURL+youtubeID, {method: "POST", 'Content-Type':  'application/json'})
        .then((response) => response.json())
        .then((json) => {
            console.log(json)
            window.open(json.url)
            this.setState({buttonDisbale: false, progressState: "None"})

        })
        .catch((error) => {
          console.error(error);
        });

    }
  }

  //clear states
  clear = () => {
    this.setState({buttonState: "Find Video", urlState: "", imgURL: "", title: ""})
  }

  //returns the query param of the url
  getParamName = (param, url) => {
    if(url.includes("?")) {
      param = param.replace(/[\[\]]/g, '\\$&');
      var regex = new RegExp('[?&]' + param + '(=([^&#]*)|&|#|$)'),
          results = regex.exec(url);
      if (!results) return null;
      if (!results[2]) return '';
      return decodeURIComponent(results[2].replace(/\+/g, ' '));
    }
    else {
      return url.substring(url.lastIndexOf("/")+1)
    }
  }

  render() {
    return(
      <div> 
        <div className="jumbotron text-center">
          <h1>Youtube to MP3 Converter</h1>
          <p>Simply enter the Youtube link you want to download, before confiming the download
            the thumbnail and title will appear of the video.</p> 
        </div>
        <h3>{this.state.title}</h3>
        <div className="mainDiv">
          <input className="form-control" placeholder="Enter Youtube URL" value = {this.state.urlState} onChange={(text) => {this.setState({urlState: text.target.value})}}></input>
          <div>
            <button className="btn btn-outline-success" disabled={this.state.buttonDisbale} onClick={this.convert}>{this.state.buttonState}</button>
            <button className="btn btn-outline-danger" disabled={this.state.buttonDisbale} onClick={this.clear}>Clear</button>
            
          </div>
          <div style={{display: this.state.progressState}}>
            <Spinner animation="border" role="status" size="sm">
              <span className="sr-only">Loading</span>
            </Spinner>
            Downloading...
          </div>
          <img src={this.state.imgURL} alt={this.state.title}></img>
        </div>

      </div>


    );
  }
}

export default App;
